<template>
<div :class="['todo-item',todo.completed?'completed':'']">
  <input
    type="checkbox"
    class="toggle"
    v-model="todo.completed"
  >
  <label>{{todo.content}}</label>
  <button class="destroy" @click="deleteTodo"></button>
</div>
</template>

<script>
    export default {
      name: "item",
      props:{
          todo:{
            type:Object,
            completed:false,
            required:true,
          }
      },
      methods:{
        deleteTodo(){
          this.$emit('del',this.todo.id)
        }
        /*vue的事件机制，vue的父组件会随时监听子组件的触发*/
      }
    }
</script>

<style scoped lang="stylus">
.todo-item {
  position: relative;
  background-color: #fff;
  font-size: 24px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06)
  &:hover {
    .destroy:after {
      content: '×';
    }
  }
  label {
    white-space: pre-line;
    word-break: break-all;
    padding: 15px 60px 15px 15px;
    margin-left: 45px;
    display: block;
    line-height: 1.2;
    transition: color 0.4s;
  }
  &.completed {
    label {
      color: #d9d9d9;
      text-decoration line-through
    }
  }
}
  .toggle {
    text-align: center;
    width: 40px;
    height: 40px;
    position: absolute;
    top: 7px;
    bottom: 0;
    margin: auto 0;
    border: none;
    -webkit-appearance: none;
    outline: none;
    &:after {
      content url("../assets/img/round.svg")
    }
    &:checked:after{
      content url("../assets/img/success.svg")
    }
  }
  .destroy{
    position: absolute;
    top:0
    right:10px
    bottom 0
    width: 40px;
    height: 40px;
    cursor pointer
    margin:auto 0
    font-size:30px
    color: #cc9a9a
    margin-bottom:11px
    transition color 0.2s ease-out;
    background-color transparent
    -webkit-appearance none
    border-width 0
    outline none
  }
</style>



